@import url("https://fonts.googleapis.com/css2?family=Roboto+Condensed&display=swap");

body {
  background-image: url("../images/background_image_half_trans_v2.png");
  background-size: cover;
  background-repeat: no-repeat;
}

.robin-salomon-name {
  color: gray;
  display: inline-flex;
  fill: #000;
  font-family: "Roboto Condensed", sans-serif;
  height: 48px;
  line-height: 30px;
  outline: 0;
  text-decoration: none;
  letter-spacing: 15px;
  font-size: 3.5rem;
  text-transform: uppercase;
}

.headshot {
  max-width: 100%;
  height: auto;
  width: 400px;
}
.robin-salomon-name {
  display: flex;
  flex-direction: row;
  justify-content: center;
  margin-top: 40vh;
}
.web-developer {
  color: gray;
  display: inline-flex;
  fill: #000;
  font-family: "Roboto Condensed", sans-serif;
  height: 48px;
  outline: 0;
  text-decoration: none;
  letter-spacing: 7px;
  font-size: 2rem;
  padding: 15px;
  display: flex;
  flex-direction: row;
  justify-content: center;
  text-transform: uppercase;
  font-weight: 400;
}

.landing-page-menu-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.menu-item {
  color: gray;
  display: inline-flex;
  fill: #000;
  font-family: "Roboto Condensed", sans-serif;
  outline: 0;
  text-decoration: none;
  letter-spacing: 7px;
  font-size: 1.5rem;
  padding: 8px;
  margin-top: -10px;
  font-weight: 400;
}
.menu-item:hover {
  cursor: pointer;
  color: white;
}
@media screen and (max-width: 601px) {
  .robin-salomon-name {
    font-size: 7vw;
    letter-spacing: 12px;
  }
  .menu-item {
    font-size: 3.5vw;
    padding: 5px;
  }
  .web-developer {
    font-size: 1.5rem;
    text-align: center;
  }
  @media screen and (max-width: 601px) {
    body {
      height: 60vh;
    }
  }
}
